<template>
    <div ref="wrapper">
        <slot></slot>
    </div>
</template>

<script>
    import BScroll from 'better-scroll'
    export default{
        props:{
            /*
                1.滚动的时候会激发scroll事件，会截液，
                2.滚动的时候实时派发scroll事件，不会截液，
                3.除了实时派发scroll事件，在swipe的情况下仍然能实现派发scroll事件
            */
           probeType:{
               type:Number,
               default:1
           },
           //点击列表是否派发click事件
           click:{
               type:Boolean,
               default: true
           },
           //是否开启横向滚动
           scrollX:{
               type:Boolean,
               default:false
           },
           //是否开启横向滚动
           scrollY:{
               type:Boolean,
               default:true
           },
           //是否派发滚动事件
           listenScroll:{
               type:Boolean,
               default:true
           },
           //列表数据
           data:{
               type:Array,
               default:null
           },
           //是否派发滚动到底部的事件，用于上拉加载
           pullup:{
               type:Boolean,
               default:false
           },
           //是否派发顶部下拉的事件，用于下拉刷新
           pulldown:{
               type:Boolean,
               default:false
           },
           //是否派发列表滚动开始的事件
           beforeScroll:{
               type:Boolean,
               default:true
           },
           //当数据更新后，刷新srcoll的延时
           refreshDelay:{
               type:Number,
               default:20
           }
        },
        mounted(){
            //保证在DOM渲染完毕后初始化better-scroll
            //console.log("DOM渲染后激活")
            setTimeout(()=>{
                this._initScroll()
            },20)
        },
        methods:{
            _initScroll(){
                if(!this.$refs.wrapper){
                    return
                }
                //consloe.log(this.$refs.wrapper)
                //better-scroll的初始化
                this.scroll = new BScroll(this.$refs.wrapper,{
                    probeType:this.probeType,
                    click:this.click,
                    scrollY:this.scrollY
                })
                if(this.listenScroll){
                    this.scroll.on('scroll',(pos)=>{
                        this.$emit('scroll',pos)
                    })
                }
                //是否派发滚动到底部事件，用于上拉加载
                if(this.pullup){
                    this.scroll.on('scrollEnd',()=>{
                        if(this.scroll.y<=(this.scroll.maxScrollY+50)){
                            this.$emit('scrollToEnd')
                        }
                    })
                }
                //是否派发顶部下拉事件，用于下拉刷新
                if(this.pulldown){
                    this.scroll.on('touchend',(pos)=>{
                        //下拉动作
                        if(pos.y>50){
                            this.$emit('pulldown')
                        }
                    })
                }
                //是否派发列表滚动开始的事件
                if(this.beforeScroll){
                    this.scroll.on('beforeScrollStart',()=>{
                        this.$emit('beforeScroll')
                    })
                }
            },
            disable(){
                //代理better-scroll的disable方法
                this.scroll && this.scroll.disable()
            },
            enable(){
                //代理beeter-scroll的enable方法
                this.scroll && this.scroll.enable()
            },
            refresh(){
                //代理better-scroll的refresh方法
                this.scroll && this.scroll.refresh()
            },
            scrillTo(){
                //代理better-scroll的scrollTo方法
                this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
            },
            scrollToElement(){
                //代理better-scroll的scrollToElement方法
                this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
            }
        },
        watch:{
            //监听数据的变化，超时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
            data(){
                setTimeout(()=>{
                    this.refresh()
                },this.refreshDelay)
            }
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>